import React, { PureComponent } from 'react';
import Error from '../_util/Error';
import Card from './components/card';
import Header from './components/header';
import Body from './components/body';
import Footer from './components/footer';
import '../style/index';
import './index.css';
export default class CardEntrance extends PureComponent {
    render() {
        return (<Error componentName='Card'>
                <Card {...this.props}/>
            </Error>);
    }
}
CardEntrance.Header = Header;
CardEntrance.Body = Body;
CardEntrance.Footer = Footer;



