import React, { PureComponent } from 'react';
import Modal from '../../modal';
import config from '../../_util/config';
import { zzcComponentUse } from '../../_util/gtag';
export default class Loading extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: this.props.visible
        };
        zzcComponentUse('Loading', '组件渲染');
    }
    getModal(div) {
        this.modal = div;
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            visible: nextProps.visible
        });
    }
    render() {
        const { prefixCls, text, closeCallback } = this.props;
        const { visible } = this.state;
        return (<Modal ref={(div) => { this.getModal(div); }} prefixCls={prefixCls} visible={visible} closeCallback={() => { closeCallback && closeCallback(); }} {...this.props}>
                <div className={`${prefixCls}-content`}>
                    <img width='50' height='42' src='data:image/gif;base64,' alt='loading'/>
                    <div className={`${prefixCls}-text`}>
                        {text}
                    </div>
                </div>
            </Modal>);
    }
}
Loading.defaultProps = {
    prefixCls: `${config.cls}-loading`,
    style: {},
    maskStyle: {},
    className: '',
    closeCallback() { },
    visible: false,
    text: ''
};



