import React from 'react';
import Dialog from '../../dialog';
import animateConfig from '../../_util/animateConfig';
import config from '../../_util/config';
import { zzcComponentUse } from '../../_util/gtag';
export default class Popup extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            showPopup: this.props.visible,
            animationTypeList: animateConfig
        };
        this.maskShowed = false;
        this.boxShowed = false;
        this.rendered = false;
        zzcComponentUse('Popup', '组件渲染');
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.visible) {
            this.setState({
                showPopup: true
            });
        }
    }
    // 获取指定样式
    getAnimationClass(direction) {
        const animation = this.state.animationTypeList[`${config.cls}-slide-${direction}`];
        if (animation) {
            return animation;
        }
        return direction ? direction : null;
    }
    boxAnimated(type) {
        this.boxShowed = type == 'enter';
        if (this.boxShowed && this.maskShowed) {
            this.renderCallback();
        }
    }
    maskAnimated(type) {
        this.maskShowed = type == 'enter';
        if (this.boxShowed && this.maskShowed) {
            this.renderCallback();
        }
    }
    renderCallback() {
        !this.rendered && this.props.renderCallback && this.props.renderCallback();
    }
    render() {
        const { visible, children, prefixCls, direction, onClose, preRender } = this.props;
        // 如果使用了预渲染模式的话，就直接渲染出Dialog
        if (this.state.showPopup || preRender) {
            return (<Dialog preRender={preRender} maskTransitionName={`${config.cls}-fade`} transitionName={this.getAnimationClass(direction)} prefixCls={prefixCls} boxClassName={`${prefixCls}-${direction}`} visible={visible} boxAnimated={(type) => { this.boxAnimated(type); }} maskAnimated={(type) => { this.maskAnimated(type); }} closeCallback={() => {
                this.setState({ showPopup: false });
                onClose && onClose();
            }} {...this.props}>
                    {children}
                </Dialog>);
        }
        return null;
    }
}
Popup.defaultProps = {
    prefixCls: `${config.cls}-popup`,
    visible: false,
    style: {},
    maskStyle: {},
    maskClose: false,
    onClose() { },
    transparent: false,
    direction: 'bottom',
    preRender: false
};



